import React, { Component } from 'react';
import styles from "../OrderForm.module.css";
import FormInput from "../../../MuiComponents/FormInput";
import FormButton from "../../../MuiComponents/FormButton";



const formValid = ({formErrors, ...rest}) => {
    let valid = true;

    Object.values(formErrors).forEach(val => {
        val.length > 0 && (valid = false);
    });

    Object.values(rest).forEach(val => {
        val === null && (valid = false)
    });

    return valid;
};

class FormStep2 extends Component {
    constructor(props){
        super(props);

        this.state = {
            error: false,
            step2FullName:null,
            email:null,
            billingStreet:null,
            suite:'',
            billingCity:null,
            billingCountry:null,
            billingZip:null,
            formErrors: {
                step2FullName:'',
                email:'',
                billingStreet:'',
                billingCity:'',
                billingCountry:'',
                billingZip:'',
            },


        };
        this.handleChangeStep2 = this.handleChangeStep2.bind(this);
        this.submitStep2 = this.submitStep2.bind(this);
    }

    handleChangeStep2(event) {
        const {name, value} = event.target;
        let formErrors = {...this.state.formErrors};

        switch (name) {
            case 'step2FullName' :
                formErrors.step2FullName =
                    value.length === 0
                        ? 'Please enter recepient full name'
                        : '';
                break;
            case 'email' :
                formErrors.email =
                    value.length === 0
                        ? 'Field is required'
                        : '';
                break;
            case 'billingStreet' :
                formErrors.billingStreet =
                    value.length === 0
                        ? 'Field is required'
                        : '';
                break;
            case 'billingCity' :
                formErrors.billingCity =
                    value.length === 0
                        ? 'Field is required'
                        : '';
                break;
            case 'billingCountry' :
                formErrors.billingCountry =
                    value.length === 0
                        ? 'Field is required'
                        : '';
                break;
            case 'billingZip' :
                formErrors.billingZip =
                    value.length === 0
                        ? 'Field is required'
                        : '';
                break;
            default:
                break;
        }
        this.setState({
            formErrors, [name]:value
        })
    }

    submitStep2=(e)=>{
        e.preventDefault();

        if(formValid(this.state) ){
            console.log('ITS OK !');
            this.setState({
                error: false,
            },  () => {
                this.props.validCheck('false');
            });

        } else {
            this.setState({
                error:true,
            }, () => {
                this.props.validCheck('true');
            });
            console.log('there is an error !')
        }
        console.log(this.state.formErrors)

    };

    render() {
        const {formErrors, error, step2FullName, email, billingStreet, billingCity, billingCountry, billingZip} = this.state;
        return (
            <div>
                <div className={styles.wrapper}>

                    <form>

                        <div className={styles.billingTitle}>
                            <h1>
                                Billing information
                            </h1>
                            <p>Same as shipping</p>
                        </div>

                        <fieldset>
                            <legend>Billing Contact</legend>
                            {formErrors.step2FullName.length > 0 && (
                                <span>
                                {formErrors.step2FullName}
                            </span>
                            )}
                            <FormInput
                                handleChange={this.handleChangeStep2}
                                name='step2FullName'
                                placeholder='Full Name'
                                classname={`${formErrors.step2FullName.length > 0 ? styles.errorInput : styles.userName} 
                                     ${error && step2FullName === null ? styles.errorInput : styles.userName}`}
                            />

                            <FormInput
                                handleChange={this.handleChangeStep2}
                                name='email'
                                placeholder='Email Address'
                                classname={`${formErrors.email.length > 0 ? styles.errorInput : styles.formInput} 
                                    ${error && email === null ? styles.errorInput : styles.formInput}`}
                            />
                        </fieldset>

                        <fieldset>
                            <legend>Billing Address</legend>
                            <FormInput
                                handleChange={this.handleChangeStep2}
                                name='billingStreet'
                                placeholder='Street Address'
                                classname={`${formErrors.billingStreet.length > 0 ? styles.errorInput : styles.formInput} 
                                    ${error && billingStreet === null ? styles.errorInput : styles.formInput}`}
                            />

                            <FormInput
                                handleChange={this.handleChangeStep2}
                                name='billingSuite'
                                placeholder={'Apt, Suite, Bldg, Gate Code. (optional)'}
                                // classname={`${formErrors.fullName.length > 0 ? styles.errorInput : styles.formInput}
                                //     ${error && step2FullName === null ? styles.errorInput : styles.formInput}`}

                            />

                            <FormInput
                                handleChange={this.handleChangeStep2}
                                name='billingCity'
                                placeholder='City'
                                classname={`${formErrors.billingCity.length > 0 ? styles.errorInput : styles.formInput} 
                                    ${error && billingCity === null ? styles.errorInput : styles.formInput}`}
                            />

                            <div className={styles.country}>
                                <FormInput
                                    handleChange={this.handleChangeStep2}
                                    name='billingCountry'
                                    placeholder='Country'
                                    classname={`${formErrors.billingCountry.length > 0 ? styles.errorInputCountry : styles.inputCountry} 
                                    ${error && billingCountry === null ? styles.errorInputCountry : styles.inputCountry}`}
                                />

                                <FormInput
                                    handleChange={this.handleChangeStep2}
                                    name='billingZip'
                                    placeholder='ZIP'
                                    classname={`${formErrors.billingZip.length > 0 ? styles.errorUserZip : styles.userZip}
                                ${error && billingZip === null ? styles.errorUserZip : styles.userZip}`}
                                />
                            </div>
                        </fieldset>

                        <FormButton
                            handleClick={this.submitStep2}
                            name='Continue'
                            style={styles.continueBtn}
                        />
                    </form>

                </div>
            </div>
        );
    }
}

export default FormStep2;

